$primaryColor: #4527a4;

// Dashicon
.dashicons {
	font-family: dashicons !important;
}

.wp-block-icb-image-compare {
	* {
		box-sizing: border-box;
	}

	.comparison-slider-wrapper,
	.icbImageCompare {
		position: relative;
		display: inline-block;
		background-color: white;
		max-width: 100%;

		.comparison-slider {
			position: relative;
			width: 100%;
			margin: 0;
			box-sizing: border-box;

			>img {
				width: 100%;
				height: auto;
				display: block;
			}

			.overlay {
				display: none;
				position: absolute;
				width: 250px;
				bottom: 20px;
				right: 20px;
				background-color: rgba(0, 0, 0, 0.4);
				box-sizing: border-box;
				text-align: right;

				@media screen and (min-width : 767px) {
					display: block;
				}
			}

			.resize {
				position: absolute;
				top: 0;
				left: 0;
				height: 100%;
				width: 50%;
				overflow: hidden;

				>img {
					display: block;
				}

				.overlay {
					right: auto;
					left: 20px;
					text-align: left;
				}
			}

			.divider {
				position: absolute;
				width: 2px;
				height: 100%;
				background-color: rgba(256, 256, 256, 0.2);
				left: 50%;
				top: 0;
				bottom: 0;
				transform: translateX(-50%);
				cursor: ew-resize;

				&:before {
					content: '';
					position: absolute;
					width: 20px;
					height: 20px;
					background-color: white;
					left: 50%;
					top: 50%;
					transform: translate(-50%, -50%) rotate(45deg);
					transition: all 0.1s ease-in-out;
				}

				// &:after {
				// 	content: '';
				// 	position: absolute;
				// 	width: 12px;
				// 	height: 12px;
				// 	background-color: white;
				// 	left: 50%;
				// 	top: 50%;
				// 	transform: translate(-50%, -50%) rotate(45deg);
				// 	transition: all 0.1s ease-in-out;
				// }

				&.draggable {
					&:before {
						width: 30px;
						height: 30px;
						left: -14px;
						margin-top: -15px;
					}

					&:after {
						width: 20px;
						height: 20px;
						left: -9px;
						margin-top: -10px;
						background-color: #555;
					}
				}
			}
		}

		.caption {
			position: relative;
			width: 100%;
		}
	}
}